@import url(https://fonts.googleapis.com/css?family=Neucha|Patrick+Hand+SC);
*{font-family:"Neucha",sans-serif;-webkit-overflow-scrolling:touch;-webkit-tap-highlight-color:transparent}html{box-sizing:border-box;font-size:16px;height:100%}*,*::before,*::after{box-sizing:inherit}html,body{-webkit-overflow-scrolling:touch}.container{max-width:1170px;width:100%;margin:0 auto}.button{text-decoration:none;text-transform:uppercase;color:#000;font-size:14px;line-height:102.25%;letter-spacing:-0.005em;cursor:pointer;border:2px solid #000;padding:8px 11px;width:100%;border-bottom-left-radius:185px 25px;border-bottom-right-radius:20px 205px;border-top-left-radius:125px 25px;border-top-right-radius:10px 205px;box-shadow:15px 28px 25px -18px rgba(0,0,0,.2);transition:all 235ms ease-in-out 0s}section+section{margin-top:65px}img{width:100%;height:auto;border:0}
.header{display:flex;justify-content:space-between;align-items:center}.header .logo{width:149px;height:90px}.header__links{margin:0;padding:0;list-style-type:none;display:flex}.header__links .active{border-bottom-left-radius:15px 3px;border-bottom-right-radius:15px 5px;border-bottom-style:solid;border-bottom-width:3px;padding-bottom:.1rem}.header__links li+li{margin-left:39px}.header__link{text-decoration:none;text-transform:uppercase;color:#000;font-size:14px;line-height:102.25%;letter-spacing:-0.005em;cursor:pointer}.hero{display:flex;align-items:center}.hero .left,.hero .right{flex:50% 1}.hero .left{display:flex;flex-direction:column}.hero__title{font-weight:700;font-size:64px;line-height:102.25%;color:#000;text-transform:uppercase;letter-spacing:-0.005em;margin-bottom:16px}.hero__subtitle{font-size:18px;line-height:102.25%;letter-spacing:-0.005em;color:#000;margin-bottom:24px;text-transform:uppercase}.hero .button{width:184px;display:flex;padding:17px;justify-content:center;font-size:18px}.section__title{font-size:45px;line-height:102.25%;font-weight:700;letter-spacing:-0.005em;text-transform:uppercase;color:#000;margin-bottom:38px;display:block}.section__title.with__subtitle{margin-bottom:14px}.section__subtitle{font-size:18px;line-height:102.25%;letter-spacing:-0.005em;text-transform:uppercase;display:block;margin-bottom:48px}.portfolio__items{display:grid;grid-template-columns:2fr 1fr 1fr;grid-template-rows:1fr 1fr;grid-template-areas:"first . ." "first . .";grid-gap:27px;gap:27px;justify-content:stretch;align-items:stretch;align-content:stretch}.portfolio__item:first-of-type{grid-area:first}.portfolio__item:first-of-type img{max-height:570px;max-width:570px}.portfolio__item img{height:100%;max-height:270px;max-width:270px;box-shadow:15px 28px 25px -18px rgba(0,0,0,.2)}.services__items{display:flex;justify-content:space-between;position:relative}.services__items .arrow{width:140px;height:140px}.services__item{display:flex;flex-direction:column;justify-content:center;align-items:center;max-width:323px;text-align:center}.services__item-img{width:150px;height:115px;margin-bottom:40px}.services__item-title{font-weight:600;font-size:25px;line-height:102.25%;letter-spacing:-0.005em;text-transform:uppercase;color:#000;margin-bottom:14px}.services__item-subtitle{font-size:18px;line-height:102.25%;letter-spacing:-0.005em;text-transform:uppercase;color:#000}
